<template>
  <div>
    <div class="value_box " style="margin-bottom: 20px;">
      <div class="value_box">
        <span></span>
         <el-select v-model="OptionData.positionName" class="w200" placeholder="">
    <el-option
      v-for="item in AddArr"
      :key="item"
      :label="item"
      :value="item">
    </el-option>
  </el-select>
      </div>
       <div class="value_box">
        <span></span>
           <el-select v-model="OptionData.shipType" class="w200" placeholder="">
    <el-option
      v-for="item in getShipTypeList"
      :key="item"
      :label="item"
      :value="item">
    </el-option>
  </el-select>
      </div>
       <div class="value_box">
        <span></span> <el-input class="w200"  placeholder=""></el-input>
      </div>
       <el-button type="primary" @click="getData"></el-button>
       <el-button type="primary" @click="AddClick"></el-button>
    </div>
  <el-table
    :data="tableData"
    style="width: 100%"
    >
    <el-table-column
      prop="positionName"
      label=""  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="contractDuration"
      label=""  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="education"
      label=""  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="monthlySalary"
      label=""  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="positionNum"
      label=""  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="shipType"
      label=""  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="updateTime"
      label=""  align="center"
      >
    </el-table-column>
    <el-table-column
      prop="statusFlag"
      label=""  align="center"
      >
       <template slot-scope="item">
         <div>{{item.row.statusFlag==0?'':''}}</div>
      </template>
    </el-table-column>

     <el-table-column label="" width="300" align="center">
      <template slot-scope="item">
         <el-button type="danger" v-if="item.row.statusFlag==='1'"
          size="mini" @click="operate(item.row.id,0)"
          ></el-button>
           <el-button type="primary"  @click="operate(item.row.id,1)" v-if="item.row.statusFlag==='0'"
          size="mini"
          ></el-button>
        <el-button type="primary"
          size="mini"
          ></el-button>
          <el-button type="danger"
          size="mini"
          ></el-button>
      </template>
    </el-table-column>
  </el-table>
  <div class="fy">
    <el-pagination
  background @current-change="pageClick"
  layout="prev, pager, next"
  :total="total">
</el-pagination>
  </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      jobStatus: [
        { label: '', value: '0' },
        { label: '', value: '1' }
      ],
      total: 0,
      tableData: [

      ],
      OptionData: {
        pageNum: 1,
        pageSize: 10
      }
    }
  },
  created () {
    this.getData()
  },
  methods: {
    operate (id, value) {
      this.$axios.post('/seaman-recruit/position/updateById', { statusFlag: value, id: id, companyId: JSON.parse(localStorage.getItem('user')).id }).then(res => {
        if (res.data.code === 200) {
          this.$message({
            type: 'success',
            message: '!'
          })
          this.getData()
          // this.$router.back()
        } else {
          this.$message.error(res.data.data)
        }
      })
    },
    pageClick (value) {
      this.OptionData.pageNum = value
      this.getData()
    },
    getData () {
      this.$axios.post('/seaman-recruit/position/queryByPage', this.OptionData).then(res => {
        if (res.data.code === 200) {
          this.tableData = res.data.data.list
          this.total = res.data.data.total
        } else {
          console.log(res.data.data)
          this.$message.error(res.data.data)
        }
      })
    },
    AddClick () {
      this.$router.push('/home/JobListings/Add')
    },
    formatter (row, column) {
      return row.address
    }
  }
}
</script>

<style lang="scss" scoped>
.value_box{
  display: flex;
  align-items: baseline;
}

::v-deep.el-table th.el-table__cell{
  background: #F8F8F9;
}
.w200{
  width: 140px;
  margin: 0 10px;
}
</style>
